.footer {
  
  max-width: 100%;

 
  &__form {
    width: 410px;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    padding-top: 70px;
    padding-bottom: 58px;
    background-image: url('/img/footer.jpg');
    background-repeat: no-repeat;
  }

  &__order {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__container {
    max-width: 930px;
    margin: 0 auto;
  }

  &__bottom {
   
    padding-top: 30px;
    padding-bottom: 30px;
    font-size: 12px;
    line-height: 20px;
    color: $white;
    background-color: $lightpurple;
  }

  &__copyright {
    display: flex;
    justify-content: space-between;
  }

  &__link {
    color: $white;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    color: $lightpurple;
    font-size: 14px;
    line-height: 20px;
  }

  &__email, 
  &__phone {
    color: $lightpurple;
  }

  &__item:not(:last-child) {
    margin-bottom: 32px;
  }

  &__item:not(:nth-child(2n)) {
    margin-right: 60px;
  }

  &__contact {
    margin-right: 180px;
  }
}